body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;


}


.container {
    display: flex;
    height: 100vh;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    flex-grow: 1;
    flex-shrink: 0;
  
  }
  
  .mini-container {
    display: flex;
    flex-grow: 0;
    min-width: 100vw;
    justify-content: center;
    flex-shrink: 0;
    cursor: none;
  
  }
  
  img {
    flex-shrink: 0;
    height: 100%;
    width: 100%;
    z-index: 1;

  }
  
  .mario {
    z-index: 6;
    height: 50px;
    width: 50px;
    border:2px dashed black;
    border-radius: 50%;
    position: absolute;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    position: fixed;
    z-index: 4;
    border: 2px solid black;
    width: 100vw;
    background-color: white;
    

  }

.image-holder {
opacity: 100%;
z-index: 7;
height:400px;
width: 250px;
border: 2px solid black;
position: absolute;
display: flex;
justify-content: center;
align-items: center;
grid-gap: 12px;
gap: 12px;
cursor: default;
background-color: white;
padding: 10px;
flex-direction: column;

}

.image-hold {
    height: 20%;
    width: 30%;
    background-color: white;
    margin: 2px;
    cursor: pointer;
}


.image-hold:hover {
    opacity: 40%;
    transition: 0.3s;
}


.status-container {
    height: 100%;
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 40px;
    gap: 40px;


}

.image-hold-two {
    height: 80%;
    width: 20%;
    transition: 0.3s;
}
.image-hold-two:hover {
    cursor:default;
}

.header-containner {
    display: flex;
    justify-content: space-around;
}

.stopwatch {
    margin: 1%;
    font-weight: bolder;
    font-size: 24px;
}
